      "İş Kanunu’nun 32. maddesine göre, ücret gününde ödenmediği taktirde, ücret alacağına en yüksek mevduat faizi uygulanır. Ücretin, ödeme gününden itibaren yirmi gün geçmiş olmasına rağmen ödenmemesi durumunda (mücbir sebepler dışında), işçinin iş görme borcunu yerine getirmekten kaçınma hakkı vardır. Birden fazla işçinin, ücretinin ödenmemesi gerekçesiyle çalışmama hakkını toplu olarak kullanması grev olarak nitelendirilemez ve işçilerin iş sözleşmeleri, çalışmama hakkını kullanmaları gerekçesiyle feshedilemez. Çalışmama hakkını kullanan işçilerin yerine başka işçiler istihdam edilemez ve bu işler başkalarına yaptırılamaz. Ancak işçi, çalışmama hakkını kullandığı süreler için ücrete hak kazanamaz.",
